.login {

  h2 {
    text-align: center;
    font-size: 34px;
  }

  .modern-form {
    width: 500px;
    margin: 30px auto;
    background-color:  #A0C6FC;
    border: solid 1px #ccc;
    padding: 20px 10px;
    display: flex;
    flex-direction: column;
  }

  .float-label-field {
    border: none;
    outline: none;
    position: relative;
    margin: 0 0 20px 0;
    padding: 0;
    box-sizing: border-box;
  }

  .float-label-field input {
    border: none;
    outline: none;
    padding: 5px 5px 8px 0;
    width: 100%;
    font-size: 18px;
    border-bottom: solid 1px #efefef;
  }

  .float-label-field label {
    opacity: 0.5;
    position: absolute;
    top: 10px;
    left: 0;
    transition: all 0.2s ease;
    font-size: 18px;
  }

  .float-label-field.focus label {
    color: orange;
  }
  .float-label-field.focus input {
    border-bottom: solid 1px orange;
  }
  .float-label-field.float label {
    opacity: 1;
    top: -8px;
    font-size: 60%;
    transition: all 0.2s ease;
    font-weight: bold;
  }

  button {
    background-color: #FEFD38;
    font-size: 24px;
    margin: 20px 150px 0 150px;
    border-bottom: 5px solid black;
    border-right: 3px solid black;
  }

  span {
    text-align: center;
    margin-top: 2%;
  }
}