.plan-choice {
  h2 {
    font-weight: bold;
    font-size: 30px;
    text-align: center;
  }

  h3 {
    font-size: 22px;
    font-weight: bold;
    border-bottom: 2px solid black;
    margin-bottom: 20px;
  }

  div {
    display: flex;
    justify-content: space-evenly;
    margin-top: 2%;
  }

  .plan {
    display: flex;
    flex-direction: column;
    padding: 5%;

    div {
      background-color: #FEFD38;
      margin-top: 5px;
      border-bottom: 5px solid black;
      border-right: 3px solid black;
      a {
        text-decoration: none;
        color: black;
        text-align: center;
      }
    }
  }

  article {
    background-color: #A0C6FC;
    border: 1px solid black;
    line-height: 1.5;
    padding: 50px;
  }

  li {
    list-style-type: square;
  }
}